// - - - - - - - - - - Foundations

@import "_reset";
@import "_mixins";
@import "_plugins";



// - - - - - - - - - - Variables


// Colors

$background-color: #ffffff;
$background-alt-color: #f4f5f6;
$border-color: #dddddd;

$text-dark-color: #000;
$text-medium-color: #111;
$text-light-color: #ABB7B7;

$accent-color: #B31B1B;

$overlay-background-color: #B31B1B;
$overlay-text-color: #ffffff;
$overlay-accent-color: #2A2F36;

$error-color: #D64541;


// Fonts

$body-font: "Poppins", sans-serif;
$title-font: "Poppins", sans-serif;

$regular-weight: 200;
$bold-weight: 400;


// Typography

$sitetitle-small: 22px;
$sitetitle-medium: 26px;
$sitetitle-large: 34px;
$sitetitle-weight: 500;
$sitetitle-spacing: 0.2em;

$menu-small: 30px;
$menu-medium: 14px;
$menu-large: 15px;
$menu-weight: 500;
$menu-spacing: 0em;

$grid-item-title-small: 18px;
$grid-item-title-medium: 22px;
$grid-item-title-large: 24px;
$grid-item-title-weight: 500;
$grid-item-title-spacing: -0.02em;

$grid-item-subtitle-small: 13px;
$grid-item-subtitle-medium: 15px;
$grid-item-subtitle-large: 17px;
$grid-item-subtitle-weight: 300;
$grid-item-subtitle-spacing: 0em;

$h1-small: 25px;
$h1-medium: 35px;
$h1-large: 45px;
$h1-weight: 500;
$h1-spacing: 0.02em;
$h1-lineheight: 1.2;

$h2-small: 22px;
$h2-medium: 30px;
$h2-large: 35px;
$h2-weight: 500;
$h2-spacing: 0.02em;
$h2-lineheight: 1.2;

$h3-small: 22px;
$h3-medium: 25px;
$h3-large: 30px;
$h3-weight: 500;
$h3-spacing: 0.02em;
$h3-lineheight: 1.3;

$h4-small: 17px;
$h4-medium: 22px;
$h4-large: 22px;
$h4-weight: 500;
$h4-spacing: 0.02em;
$h4-lineheight: 1.6;

$h5-small: 17px;
$h5-medium: 20px;
$h5-large: 20px;
$h5-weight: 500;
$h5-spacing: 0.02em;
$h5-lineheight: 1.6;

$h6-small: 17px;
$h6-medium: 20px;
$h6-large: 20px;
$h6-weight: 500;
$h6-spacing: 0.02em;
$h6-lineheight: 1.6;

$p-small: 12px;
$p-medium: 14px;
$p-large: 17px;
$p-weight: 100;
$p-spacing: 0em;
$p-lineheight: 1.7;

$blockquote-small: 15px;
$blockquote-medium: 22px;
$blockquote-large: 30px;
$blockquote-weight: 300;
$blockquote-spacing: -0.02em;
$blockquote-lineheight: 1.6;

$button-weight: 500;
$button-spacing: 0em;


// Logo
$logo-width: 400px;
$footer-logo-width: px;


// Grid item
$overlay-opacity: 0.7;
$grid-spacing: 20px;



// - - - - - - - - - - Global styles

@import "_basic";

@import "_includes/_header";
@import "_includes/_footer";

@import "_includes/_content";
@import "_includes/_socials";
@import "_includes/_gallery";
@import "_includes/_syntax";
@import "_includes/_listing";
@import "_includes/_contact-form";


